//pagination
$pagination-active-bgr: $primary-color;
$pagination-fz: 16px;





.pagination {
	display: flex;
	align-items: baseline;
	margin-top: 40px;
}
.pagination__label {
	margin-right: $gap-md;
}
.pagination__wrap {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 0 - $gap-sm;
}
.pagination__item {
	font-size: $pagination-fz;
	padding: 5px 5px;
	margin: 0 0 $gap-sm 0;
	min-width: 24px;
	text-align: center;
	border: 1px solid $pagination-active-bgr;
	color: #333;
	border-radius: 2px;
	margin-right: 10px;
	&:last-child {
		margin-right: 0;
	}
}
.pagination__item_active {
	color: #fff;
	background-color: $pagination-active-bgr;
}
