.catalog-items {
	margin-bottom: 30px;
}
.catalog-items__wrap {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: $gap-lg;
	@media (max-width: $bp2) {
		grid-template-columns: repeat(3, 1fr);
	}
	@media (max-width: $bp4) {
		grid-template-columns: 1fr 1fr;
	}
	@media (max-width: $bp5) {
		grid-template-columns: 1fr;
	}
}
.catalog-item {
	border: 1px solid $gray-3;
	position: relative;
	border-radius: 8px;
	overflow: hidden;
}
.catalog-item__image {
	img {
		padding: 20px;
	}
}
.catalog-item__info {
	padding: $gap-md;
}
.catalog-item__type {
	font-size: 12px;
	color: #828282;
}
.catalog-item__title {
	font-weight: 700;
	margin-bottom: $gap-sm;
	position: relative;
	line-height: 1.3;
}
.catalog-item__text {
	font-size: 13px;
	line-height: 1.3;
	color: $gray-1;
}
.catalog-item__link {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
