.title {
	font-weight: $title-fw;
	margin: $title-m;
}
.title_1, h1 {
	font-weight: $title-1-fw;
	font-size: $title-1-fz;
	margin: $title-1-m;
}
.title_2, h2 {
	font-size: $title-2-fz;
	font-weight: $title-2-fw;
	margin: $title-2-m;
}
.title_3, h3 {
	font-size: $title-3-fz;
	font-weight: $title-3-fw;
	margin: $title-3-m;
}
.title {
	&_block {
		font-size: 36px;
		font-weight: 700;
		margin-bottom: 30px;
	}
}

.text_size_sm {
	font-size: 11px;
	color: $gray-2;
}

.text {
	color: #333;
	ul {
		padding-left: 32px;
	}
	li {
		list-style: disc;
		margin-bottom: 16px;
		line-height: 1.2;
	}
}
p {
	margin-bottom: 10px;
}
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap');
