.main-menu {
	margin-bottom: 20px;
	@media (max-width: $bp3) {
		display: none;
	}
}
.main-menu__wrap {
	display: flex;
	align-items: center;
}
.main-menu__item {
	height: 54px;
	flex: 1 1 auto;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 14px;
	display: flex;
	position: relative;
	background-color: $primary-color;
	&:hover {
		background-color: darken($primary-color, 5%);
	}
	span {
		font-size: 24px;
	}
	.icon {
		height: unset !important;
	}
	&:first-child {
		background-color: $secondary-color;
		border-radius: 4px 0 0 4px;
		&:hover {
			background-color: darken($secondary-color, 5%);
		}
	}
	&:last-child {
		border-radius: 0 4px 4px 0;
	}
	> a {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;
	}
}



// popup menu
.popup-catalog-menu {
	position: absolute;
	background-color: #f2f2f2;
	z-index: 2;
	top: 54px;
	left: 0;
	display: flex;
	box-shadow: 0 10px 15px rgba(0,0,0,.2);
	a {
		padding: $gap-sm;
		font-weight: 400;
		text-transform: none;
		&:hover {
			background-color: #fff;
		}
	}
}
.sections {
	width: 200px;
	display: flex;
	flex-direction: column;
	padding: $gap-sm 0;
}
.subsections {
	display: flex;
	flex-direction: column;
	width: 250px;
	background-color: #fff;
	padding: $gap-sm 0;
}
.popup-text-menu {
	@extend .popup-catalog-menu;
}
// popup menu end
