.side-menu {
	@media(max-width: $bp3) {
		display: none;
	}
}
.side-menu__item {
	padding: 8px 0;
	position: relative;
	display: flex;
	&:before {
		display: block;
		width: 10px;
		height: 1px;
		background-color: $primary-color;
		margin-right: 10px;
		margin-top: 7px;
	}
}
.side-menu__item_level_1 {
	font-size: 16px;
}
.side-menu__link_active {
	font-weight: 700;
	color: #000; //var
}
.side-menu__item_level_2 {
	margin-left: 0;
	font-size: 15px;
	&:before {
		content: '';
	}
}
.side-menu__item_level_3 {
	margin-left: 25px;
	font-size: 14px;
	&:before {
		content: '';
	}
}
.side-menu__item_level_4 {
	margin-left: 45px;
	font-size: 14px;
	&:before {
		content: '';
	}
}
