.devices-block__wrap {
	display: grid;
	grid-template-columns: repeat(6, 1fr);
	grid-gap: 60px 20px;
}
.devices-block__column {
	padding: 0 8px;
	min-width: 0px;
	display: flex;
}

.device {
	position: relative;
	display: flex;
	flex-direction: column;
	overflow: hidden;

	> * {
		margin-bottom: $gap-sm;
		&:last-child {
			margin-bottom: 0;
		}
	}
	form {
		margin-top: auto;
	}
}
.device__image {
	margin-top: -16px;
	margin-right: -16px;
	margin-left: -16px;
}
.device__tags {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	flex-direction: column;
	padding-right: $gap-lg;
	align-items: flex-start;
	max-width: 100%;
}
.device__tag {
	margin-bottom: $gap-xs;
	&:last-child {
		margin-bottom: 0;
	}
}
.device__name {
	display: block;
	line-height: 1.3;
	font-size: 14px;
}
.device__price {
	display: flex;
	font-weight: bold;
	align-items: baseline;
	> div {
		font-size: 20px;
		margin-right: $gap-sm;
		@media (max-width: $bp4) {
			font-size: 18px;
		}
		&:last-child {
			margin-right: 0;
		}
		&:nth-child(2) {
			font-size: 14px;
			font-weight: 400;
		}
	}
}
.price__item_old {
	.price__value {
		color: $gray-2;
		position: relative;
		&:before {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 1px;
			background-color: rgb(204, 32, 32);
			top: 50%;

		}
	}
}
.order {
	display: flex;
	form {
		display: flex;
		> * {
			margin-right: $gap-xs;
			&:last-child {
				margin-right: 0;
			}
		}
	}
}
.device-order {
	margin-top: auto;
}
.add {
	display: flex;
	> * {
		margin-right: $gap-xs;
		&:last-child {
			margin-right: 0;
		}
	}
}
.add__item {
	align-items: center;
	font-size: 12px;
	.icon {
		height: 12px;
		margin-right: 0;
	}
}










.device_row {
	flex-direction: row;
	text-align: left;
	border-bottom: 1px solid $gray-2;
	&:last-child {
		border: none;
	}
	> * {
		margin-bottom: 0;
	}
	.device__name {
		font-size: 18px;
		margin-bottom: 0;
	}
	.device__image {
		width: 150px;
		margin: 0;
	}
	.device__code {
		font-size: 13px;
		color: #828282;
	}
	.price {
		white-space: nowrap;
		flex-direction: column;
		&__item {
			&_old {
				margin-top: 10px;
			}
		}
	}
	.order {
		justify-content: flex-end;
	}
	.add {
		flex-direction: column;
		justify-content: unset;
	}
	.add__item {
		margin-bottom: $gap-sm;
		margin-right: 0;
	}
}
.device__column {
	margin-right: $gap-md;
	&:last-child {
		margin-right: 0;
	}
	&:nth-child(2) {
		flex: 1 1 auto;
	}
	> * {
		margin-bottom: $gap-md;
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.device__info {
	.params {
		tr {
			display: block;
			border: none;
		}
		td {
			font-size: 16px;
			display: inline-block;
			padding: 2px;
			font-weight: 400;
			&:first-child {
				color: $gray-2;
			}
		}
	}
}













//============= slick =================

.device {
	min-width: 0;
}

.device-nav {
	padding-top: $gap-sm;
	.slick-dots {
		display: flex;
		justify-content: center;
		align-items: center;
		li {
			margin-right: 8px;
			font-size: 0;
			button {
				font-size: 0;
				width: 11px;
				height: 11px;
				border-radius: 50%;
				background-color: $gray-2;
			}
			&:last-child {
				margin-right: 0;
			}
		}
		.slick-active {
			button {
				background-color: $primary-color;
			}
		}
	}

}
