//view



.view {
	font-size: 16px;
}
.view_section {
	display: flex;
	align-items: center;
	margin-bottom: 30px;
	border-bottom: 2px solid #333;
	padding-bottom: $gap-sm;
	@media (max-width: $bp4) {
		display: none;
	}
}
.view__column {
	margin-right: 40px;
	&:first-child {
		a {
			margin-right: 10px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	&:last-child {
		margin-left: auto;
		margin-right: 0;
	}
}
